<template>
  <div class="layout">
    <div class="header">
      <div class="logo">
        溜飞天在线考试网
      </div>
    </div>
    <div class="content">
      <div class="body">
        <div style="padding-top:80px;">
          <mu-text-field label="考生号" labelFloat/><br/>
          <mu-text-field label="密码" hintText="请输入密码" type="password" labelFloat/><br/>
          <mu-raised-button label="登 陆" class="demo-raised-button" :disabled="toNext" primary style="margin-top: 40px;" to="/front/regular"/>
        </div>
      </div>
    </div>
    <div class="footer">
      数据库课设 ©2017 Created by Muse-UI
    </div>

  </div>
</template>
<script>
export default {
  data () {
    return {
      activeTab: 'tab1',
      userId: '1505120515',
      userName: '汤姆',
      sex: '男'
    }
  },
  methods: {
    handleTabChange (val) {
      this.activeTab = val
    }
  }
}
</script>
<style scoped>
.layout{
  background-color: rgb(236, 236, 236);
}

.header{
  background-color: #7e57c2;
}

.logo{
  font-size: 24px;
  color: white;
  display: inline-block;
  padding: 10px 20px;
}

.nav{
  display: inline-block;
  width: calc(100% - 150px);
  margin: 0 auto;
}

.tab{
  margin: 0 auto;
  width: 400px;
  background-color: rgba(0, 0, 0, 0);
}

.content{
  width: 90%;
  margin: 0 auto;
}

.breadcrumb{
  margin: 10px 0;
}

.body{
  background-color: white;
  border-radius: 5px;
  min-height: 500px;
}

.footer{
  padding: 20px 0;
  text-align: center;
}
</style>


