<template>
<div>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th tooltip="考生号">考生</mu-th>
        <mu-th tooltip="姓名">姓名</mu-th>
        <mu-th tooltip="考试科目">考试科目</mu-th>
        <mu-th tooltip="分数">分数</mu-th>
        <mu-th>操作</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.studentNumber}}</mu-td>
        <mu-td>{{item.name}}</mu-td>
        <mu-td>{{item.subject}}</mu-td>
        <mu-td>{{item.score}}</mu-td>
        <mu-td>
          <mu-dropDown-menu :value="value" @change="handleChange">
            <mu-menu-item value="1" title="编辑"/>
            <mu-menu-item value="2" title="添加"/>
            <mu-menu-item value="3" title="删除"/>
          </mu-dropDown-menu>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
</div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [
        {
          studentNumber: '1505120206',
          name: 'John Smith',
          sexType: '男',
          subject: '数据库',
          score: 90
        },
        {
          studentNumber: '1505120206',
          name: 'John Smith',
          sexType: '男',
          subject: '数据库',
          score: 90
        },
        {
          studentNumber: '1505120206',
          name: 'John Smith',
          sexType: '男',
          subject: '数据库',
          score: 90
        },
      ],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      value: '1'
    }
  },
  methods: {
    handleChange (value) {
      this.value = value
      if(value == 2){
        
      }
    }
  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


