<template>
<div>
  <mu-table :fixedFooter="fixedFooter" :fixedHeader="fixedHeader" :height="height"
    :enableSelectAll="enableSelectAll" :multiSelectable="multiSelectable"
    :selectable="selectable" :showCheckbox="showCheckbox">
    <mu-thead slot="header">
      <mu-tr>
        <mu-th>教室编号</mu-th>
        <mu-th>教师姓名</mu-th>
        <mu-th>教师主码</mu-th>
        <mu-th>科目</mu-th>
        <mu-th>操作</mu-th>
      </mu-tr>
    </mu-thead>
    <mu-tbody>
      <mu-tr v-for="item,index in tableData"  :key="index" :selected="item.selected">
        <mu-td>{{item.roomNumber}}</mu-td>
        <mu-td>{{item.teachername}}</mu-td>
        <mu-td>{{item.teacherMain}}</mu-td>
        <mu-td>{{item.subject}}</mu-td>
        <mu-td>
          <mu-dropDown-menu :value="value" @change="handleChange">
            <mu-menu-item value="1" title="编辑"/>
            <mu-menu-item value="2" title="添加"/>
            <mu-menu-item value="3" title="删除"/>
          </mu-dropDown-menu>
        </mu-td>
      </mu-tr>
    </mu-tbody>
  </mu-table>
</div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [
        {
          roomNumber: '0001',
          teachername: '李晓林',
          teacherMain: '1106120203',
          subject: '数据库'
        },
        {
          roomNumber: '0001',
          teachername: '李晓林',
          teacherMain: '1106120203',
          subject: '数据库'
        },
        {
          roomNumber: '0001',
          teachername: '李晓林',
          teacherMain: '1106120203',
          subject: '数据库'
        },
      ],
      fixedHeader: true,
      fixedFooter: false,
      selectable: false,
      multiSelectable: false,
      enableSelectAll: false,
      showCheckbox: false,
      height: '500px',
      value: '1'
    }
  },
  methods: {
    handleChange (value) {
      this.value = value
      if(value == 2){
        
      }
    }
  }
}
</script>

<style lang="css">
.demo-table-settings{
  width: 200px;
  overflow: hidden;
  margin: 20px auto 0px;
}
</style>


