<template>
<div>
  <mu-select-field v-model="game1" :labelFocusClass="['label-foucs']" label="选择添加科目">
    <mu-menu-item v-for="text,index in list" :key="index" :value="index" :title="text" />
  </mu-select-field>
  <mu-raised-button label="选择题" class="demo-raised-button" style="margin-left: 40px;" @click="selectProType(1)"/>
  <mu-raised-button label="判断题" class="demo-raised-button" style="margin-left: 20px;" @click="selectProType(0)"/>
  <br/>
  <mu-text-field hintText="请输入题干" v-model="title"/><br/>
  <div :style="showSelect">
    <mu-text-field hintText="A 选项" v-model="anA"/><br/>
    <mu-text-field hintText="B 选项" v-model="anB"/><br/>
    <mu-text-field hintText="C 选项" v-model="anC"/><br/>
    <mu-text-field hintText="D 选项" v-model="anD"/><br/>
    <mu-checkbox name="group" nativeValue="A" v-model="answer" label="A" class="demo-checkbox"/>
    <mu-checkbox name="group" nativeValue="B" v-model="answer" label="B" class="demo-checkbox"/>
    <mu-checkbox name="group" nativeValue="C" v-model="answer" label="C" class="demo-checkbox"/>
    <mu-checkbox name="group" nativeValue="D" v-model="answer" label="D" class="demo-checkbox"/>
    <div class="demo-checkbox">
      正确答案：{{answer}}
    </div>
  </div>
  <div :style="showJudge">
    <mu-radio label="对" name="group" nativeValue="对" v-model="judgeVal" class="demo-radio"/> <br/>
    <mu-radio label="错" name="group" nativeValue="错" v-model="judgeVal"  class="demo-radio"/> <br/>
    <div class="demo-checkbox" style="margin-top: 30px;">
      正确答案：{{judgeVal}}
    </div>
  </div>
  <mu-raised-button label="提交题目" class="demo-raised-button" style="margin-top: 20px;" primary/>
</div>
</template>
<script>
export default {
  data () {
    return {
      game1: 0,
      game2: ['1'],
      game3: '3',
      list: ['计算机组成原理', '数据库', '算法设计与分析', '无线传感器', '计算机操作系统', '软件工程'],
      answer: [],
      title: '',
      anA: '',
      anB: '',
      anC: '',
      anD: '',
      showSelect: 'display:block;',
      showJudge: 'display:none;',
      judgeVal: ''
    }
  },
  methods: {
    selectProType(val){
      //选择题目类型
      if(val){
        this.showSelect = 'display:block;'
        this.showJudge = 'display:none;'
      }else{
        this.showJudge = 'display:block;'
        this.showSelect = 'display:none;'
      }
    }
  }
}
</script>


